.App {
  background-color: white;
  text-align: center;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.Header {
  color:rgb(157, 198, 245);
  position:fixed;
  z-index: 10;
  top:0;
  left: 0;
  width: 100%;
}

.Content {
  background-color: rgba(113, 170, 192, 0.247);
  top:0;
  left: 0;
}


.Footer {
  position: fixed;
  min-width: 100%;
  bottom: 0;
}


@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}



.slopeBegin {
  background-color: rgba(103, 147, 165,0.5);
  clip-path: polygon(0 0, 100% 0, 19% 100%, 0 100%);
  
}

.slopeEnd {
  background-color: rgba(127, 186, 209);
  clip-path: polygon(100% 0%, 100% 100%, 19% 100%);
}

.slopeBegin,
.slopeEnd {
  
  position: absolute;
  width: 100%;
  height: 150%;
  cursor: pointer;
}
